import * as React from 'react';
import Pagination from '@mui/material/Pagination';
import Stack from '@mui/material/Stack';

export default function PaginationOutlined({setPage, numOfPages = 10}) {
    const handlePageChange = (page) => {
        setPage(page);
        window.scroll(0, 0);
      };
  return (
      <div style={{
            width: "100%",
            display: "flex",
            justifyContent: "center",
            marginTop: 10,
            color: "#fff",
        }}> 
            <Stack spacing={2}>
                <Pagination count={numOfPages} variant="outlined" onChange={(e) => handlePageChange(e.target.textContent)} color="secondary" />
            </Stack>
      </div>
  );
}
